











import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";
@Component
export default class WhichTime extends Vue {
  @Prop(String) value: string | undefined;
  time = this.value;
  year() {
    this.time = "year";
  }
  month() {
    this.time = "month";
  }
  day() {
    this.time = "day";
  }
  @Watch("time")
  onTimeChanged() {
    this.$emit("update:value", this.time);
  }
  isClass() {
    return {
      tab: true,
      year: this.time === "year",
      month: this.time === "month",
      day: this.time === "day",
    };
  }
}
