









import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
@Component
export default class Type extends Vue {
  @Prop(String) type: string | undefined;
  myType = this.type;
  isClass(item: string) {
    return {
      selected: this.type === item,
    };
  }
  choose(str: string) {
    this.myType = str;
    this.$emit("update:type", str);
  }
}
